/* stylelint-enable */
*,
*::before,
*::after {
  box-sizing: border-box;
}
.page {
  box-sizing: border-box;
  height: 100%;
}
.body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  min-height: 100%;
  margin: 0;
  padding: 0;
  font-size: 16px;
  font-family: "Blogger Sans", "Arial", sans-serif;
  font-weight: 400;
  line-height: 1.375;
  color: #ffffff;
}
@media (max-width: 767px) {
  .body {
    overflow-x: hidden;
  }
}
@media (min-width: 768px) {
  .body {
    font-size: 28px;
    line-height: 1.17857;
  }
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.wrapper,
.wrapper-form {
  width: 100%;
  min-width: 320px;
  margin: 0 auto;
  padding: 0 25px;
}
@media (min-width: 768px) {
  .wrapper,
  .wrapper-form {
    min-width: 768px;
    padding: 0 40px;
  }
}
@media (min-width: 1440px) {
  .wrapper,
  .wrapper-form {
    max-width: 1440px;
    padding: 0 60px;
  }
}
@media (min-width: 768px) {
  .wrapper-form {
    min-width: 768px;
    padding: 0 75px;
  }
}
@media (min-width: 1440px) {
  .wrapper-form {
    max-width: 1440px;
    padding: 0 60px;
  }
}
.zero-intervals {
  margin: 0;
  padding: 0;
}
.button {
  display: inline-flex;
  min-height: 40px;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  line-height: 1;
  font-weight: 700;
  color: #1d2e5b;
  background: url(../img/button-arrow.png) center right 5% no-repeat;
  background-color: #ffd74b;
  border-radius: 10px;
  cursor: pointer;
  outline: 0;
}
.header {
  padding-top: 19px;
  background-color: #161c35;
}
@media (min-width: 768px) {
  .header__logo-ico {
    width: 166px;
    height: 42px;
  }
}
@media (min-width: 1440px) {
  .header__logo-ico {
    width: 200px;
    height: 52px;
  }
}
.social {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  max-width: 140px;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 768px) {
  .social {
    max-width: 200px;
  }
}
.social__item {
  position: relative;
  width: 45px;
  height: 45px;
  margin-bottom: 5px;
  background-color: #ffd74b;
  border-radius: 50%;
}
@media (min-width: 768px) {
  .social__item {
    width: 62px;
    height: 62px;
  }
}
.social__item:hover,
.social__item:focus,
.social__item:active {
  background-color: #ffbd30;
}
.social__link {
  display: block;
  width: 45px;
  height: 45px;
}
@media (min-width: 768px) {
  .social__link {
    width: 62px;
    height: 62px;
  }
}
.social__item:active .social__link:active {
  opacity: 0.3;
}
.social__ico {
  position: absolute;
}
.social__item:first-of-type .social__ico {
  top: 10px;
  left: 19px;
}
.social__item:nth-of-type(2) .social__ico {
  top: 16px;
  left: 12px;
}
.social__item:nth-of-type(3n) .social__ico {
  top: 13px;
  left: 14px;
}
.contacts {
  position: relative;
}
.contacts::before,
.contacts::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  background-color: #ffd74b;
}
@media (min-width: 1440px) {
  .contacts::before,
  .contacts::after {
    min-width: 100%;
  }
}
.contacts::before {
  height: 100%;
}
.contacts::after {
  top: 29%;
  z-index: 0;
  height: 50px;
  border-radius: 0 0 20px 20px;
}
@media (min-width: 768px) {
  .contacts::after {
    top: 36%;
    height: 47px;
    border-radius: 0 0 30px 30px;
  }
}
@media (min-width: 1440px) {
  .contacts::after {
    top: 25%;
    height: 56px;
    border-radius: 0 0 40px 40px;
  }
}
.contacts__info {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
@media (min-width: 1440px) {
  .contacts__info {
    flex-direction: row;
    justify-content: center;
    padding-top: 116px;
  }
}
.contacts__text {
  position: relative;
  margin-bottom: 32px;
  padding-top: 63px;
  padding-left: 61px;
  text-transform: uppercase;
  font-weight: 700;
  color: #1d2e5b;
}
@media (min-width: 768px) {
  .contacts__text {
    align-self: flex-start;
    margin-bottom: 62px;
    padding-top: 124px;
    padding-left: 94px;
    line-height: 1;
  }
}
@media (min-width: 1440px) {
  .contacts__text {
    align-self: center;
    margin-right: 147px;
    margin-bottom: 0;
    padding-top: 0;
    padding-left: 121px;
  }
}
.contacts__text::before {
  content: "";
  position: absolute;
  top: 60px;
  left: 0;
  width: 45px;
  height: 45px;
  background: url(../img/map/icon-location.svg) no-repeat;
  background-size: cover;
}
@media (min-width: 768px) {
  .contacts__text::before {
    top: 100px;
    width: 68px;
    height: 68px;
  }
}
@media (min-width: 1440px) {
  .contacts__text::before {
    top: -36px;
    width: 90px;
    height: 90px;
  }
}
.contacts__button {
  justify-content: flex-start;
  z-index: 1;
  align-self: stretch;
  min-height: 62px;
  padding-left: 22px;
  border: 3px solid #192144;
}
@media (min-width: 768px) {
  .contacts__button {
    min-height: 80px;
    margin-bottom: 37px;
    padding-left: 37px;
    font-size: 24px;
    line-height: 1;
  }
}
@media (min-width: 1440px) {
  .contacts__button {
    align-self: center;
    min-width: 324px;
    margin-bottom: 0;
  }
}
.contacts__static-map {
  z-index: -2;
  width: 100%;
}
.contacts__static-map-image {
  display: inline-block;
  vertical-align: bottom;
  width: 100%;
  height: 424px;
  object-fit: cover;
}
@media (min-width: 768px) {
  .contacts__static-map-image {
    height: 581px;
  }
}
@media (min-width: 1440px) {
  .contacts__static-map-image {
    height: 584px;
  }
}
.footer {
  position: relative;
}
.footer::before {
  content: "";
  position: absolute;
  top: -35px;
  left: 0;
  width: 100%;
  height: 35px;
  background-color: #ffffff;
  border-radius: 20px 20px 0 0;
}
@media (min-width: 768px) {
  .footer::before {
    top: -45px;
    height: 46px;
    border-radius: 40px 40px 0 0;
  }
}
.footer__block {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 18px;
}
@media (min-width: 768px) {
  .footer__block {
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 32px;
  }
}
@media (min-width: 1440px) {
  .footer__block {
    margin-bottom: 43px;
  }
}
.footer__logo-ico {
  flex-shrink: 0;
  width: 96px;
  height: 14px;
  margin-bottom: 40px;
}
@media (min-width: 768px) {
  .footer__logo-ico {
    width: 164px;
    height: 42px;
    margin-bottom: 0;
  }
}
@media (min-width: 1440px) {
  .footer__logo-ico {
    width: 200px;
    height: 55px;
  }
}
.footer__logo-ico:focus,
.footer__logo-ico:hover {
  opacity: 0.6;
}
.footer__logo-ico:active {
  opacity: 0.3;
}
.footer__developer {
  position: relative;
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-top: 50px;
  color: #444444;
  cursor: pointer;
}
@media (min-width: 768px) {
  .footer__developer {
    justify-content: flex-end;
    width: 155px;
    margin-top: 0;
    font-size: 20px;
    line-height: 1;
  }
}
@media (min-width: 768px) {
  .footer__developer-ico {
    margin-left: 23px;
  }
}
.footer__developer:focus,
.footer__developer:hover {
  opacity: 0.6;
}
.footer__developer:active {
  opacity: 0.3;
}
.footer__developer::before {
  content: "";
  position: absolute;
  top: -13px;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #f2f2f2;
}
@media (min-width: 768px) {
  .footer__developer::before {
    content: none;
  }
}
